<template>
    <div class="page">
        <div class="cont">
            <div>
                <p>持卡人姓名</p>
                <input type="text" @input="inputChange(20,'cardUserName')" v-model="cardUserName" placeholder="请输入持卡人姓名">
            </div>
            <div>
                <p>开户行</p>
                <input type="text" v-model="bankName" placeholder="请输入开户行名称">
            </div>

            <div>
                <p>银行卡号</p>
                <input type="tel" v-model="cardNo" @input="changeTel(cardNo, 'cardNo')" placeholder="请输入银行卡号">
            </div>
            <div>
                <p>预留手机号</p>
                <input type="tel" maxlength="11" @input="changeTel(cardPhone, 'cardPhone')" v-model="cardPhone" placeholder="预留手机号">
            </div>
            <div class="footer">
                <button @click="protocolUpdate()">确认</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "card",
        data() {
            return {
                cardUserName: null,
                cardNo: null,
                cardPhone: null,
                bankName: null,
            }
        },
        methods: {
            changeTel(val, memberAccount) {
                val = val.replace(/(^\s*)|(\s*$)/g, "")
                if (!val) {
                    this[memberAccount] = "";
                    return
                }
                var reg = /[^\d]/g
                val = val.replace(reg, "")
                this[memberAccount] = val;
            },
            inputChange(num, id){
                this[id] = this[id].slice(0,num)
            },
            protocolUpdate() {
                if (!this.cardUserName) {
                    this.$toast('请输入姓名')
                    return false
                }

                if (!this.bankName) {
                    this.$toast('请输入开户行名称')
                    return false
                }
                if (!this.cardNo) {
                    this.$toast('请输入卡号')
                    return false
                }
                if (!this.cardPhone) {
                    this.$toast('请输入手机号')
                    return false
                }

                this.$api.protocolUpdate({
                    cardUserName: this.cardUserName,
                    cardNo: this.cardNo,
                    cardPhone: this.cardPhone,
                    bankName: this.bankName,
                }).then(() => {
                    history.back();
                })
            },
        }
    }
</script>

